.m-center{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
}

.m-center-text{
    text-align: center;
}

.m-nav{
    width: 100%;
    height: 80px;
    color: white;
    position: fixed;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.m-nav ul li{
    display: inline-block;
    list-style: none;
}

.m-nav ul li a{
    padding: 10px;
    position: relative;
}

.m-nav ul li a:hover{
    cursor: pointer;
}

.m-nav ul li a::after,
.m-nav ul li a::before{
    content: "";
    transition: all .5s;
    background-color: white;
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    transform: scaleX(0);
}

.m-nav ul li a::before{
    top: 0;
    transform-origin: left;
}

.m-nav ul li a::after{
    bottom: 0;
    transform-origin: right;
}

.m-nav ul li a:hover::after,
.m-nav ul li a:hover::before{
    transform: scaleX(1);
}


.m-white-border{
    border-top: 1px solid white;
    border-bottom: 1px solid white;
}

.m-box-shadow-top{
    -moz-box-shadow:    inset 0 35px 25px #000000;
    -webkit-box-shadow: inset 0 35px 25px #000000;
     box-shadow:         inset 0 35px 25px #000000;
}

.m-left-text{
    text-align:left;
    padding: 10px;
}

.m-margin-right{
    margin-left: 6%;
}

.m-btn{
    font-size: 1em;
    border-radius: 30px;
    transition: background-color 0.5s linear;
}

.m-btn.md{
    padding: 10px;
    width: 250px;
}


.m-btn.xl{
    padding: 10px;
    width: 400px;
}


.m-main-text{
    font-size: 1.3em;
    width: calc(40*(1.3em/1.618));
    line-height: calc(1.5em * 1.618);
}

@media (max-width: 900px){
    .m-left-text{
        text-align: center;
    }

    .m-margin-right{
        margin:0
    }

    .m-btn.xl{
        width: 300px;
    }s

    .m-center.home-contact{
        flex-direction: column;
    }

    .m-main-text{
        font-size: 1.2em;
        line-height: calc(1.2em * 1.618);
        width: calc(25*(1.2em/1.618));
    }
}